@border-color: #54504e;
body {
	font-family: -apple-system,'SF UI Text',Arial,'PingFang SC','Hiragino Sans GB','Microsoft YaHei','WenQuanYi Micro Hei',sans-serif;
}
* {
	// touch-action: none;
}
.fill {
	position: absolute;
	left: 0;
	top:0;
	right:0;
	bottom:0;
}
.flexCenter {
	display: flex;
	justify-content: center;
	align-items: center;
}
.block {
	margin: 0 4%;
	display: flex;
	justify-content: space-between;
}

#home {
	.fill();
	.flexCenter();
	background:#53a9b4;
	// background:#53a9b4 url('./img/loading.gif') no-repeat center center;
	background-size: contain;
	z-index: 10000;
	transition: all 0.5s ease-out;
	&.hide {
		opacity: 0;
		z-index: 0;
	}
	>img {
		max-width: 100%;
		max-height: 100%;
	}
	
}

#main {
	display: flex;
	position: fixed;
	left:0;top:0; bottom: 0; right:0;
	flex-direction: column;
	background: #FFF;
	.bg_gray {
		flex: 0 0 auto;
		display: flex;
		flex-direction: column;
		// justify-content: space-evenly;
	// 	#canvas {
	// 		flex: 1 1 auto;
	// 	}
	}
	.imgbox {
		flex: 0 0 auto;
	}
	#bottom {
		flex: 1 1 auto;
		height: 25vw;
	}
}

img {
	max-width: 100%;
}
#top {
	box-sizing:border-box;
	width:100%;
	// height: 10vw;
	line-height: 10vw;
	display: flex;
	text-align: center;
	background: #fff;
	border: 1px solid @border-color;
	flex: 0 0 auto;
	> div {
		border-left: 1px solid @border-color;
	}
	.text {
		flex: 1 1 100%;
		font-size: 4vw;
		color: #333;
		border: 0 none;
		text-shadow: 0 0 2vw #fff;
	}
	.sort {
		width: 36vw;
		font-size: 4.5vw;
		flex: 0 0 auto;
		strong {
			font-size: 5vw;
			font-weight: bold;
		}
	}
	.share {
		flex: 0 0 auto;
		width: 10vw;
		height: 10vw;
		background: url('./img/icon/图标-61.png') center no-repeat #dedede;
		background-size: 80% 80%;
		vertical-align: top;
	}
}

#bottom {
	// position: fixed;
	height: 0;
	background: url('./img/banner.png') center center no-repeat #fff;
	background-size: contain;
	z-index: -1;
}


.bg_gray {
	background: #efefef;
}
#canvas {
	width: 82%;
	padding-top: 82%;
	height: 0;
	// margin: 0 auto 0 auto;
	box-sizing: content-box;
	border: 1px solid @border-color;
	// border-radius: 2%;
	overflow: hidden;
	background: #bbb;
	position: relative;
	&.hide {display: none}
	.body {
		.fill();
	}
	// canvas {
	// 	position: absolute;
	// 	width: 100%;
	// 	height: 100%;
	// 	left: 0;
	// 	top: 0;
	// 	border: 0 none;
	// }
	img {
		display: none;
	}
	
}

#outImgBox {
	width: 82%;
	font-size:0;
	&.hide {display: none}
}

.btns {
	width: 14%;
	display:flex;
	flex-wrap: wrap;
	align-self: stretch;
	align-content: flex-start;
	// background: #FFF;
	>div {
		transform: scale(0.9);
		flex: 0 0 auto;
		font-size: 3vw;
		line-height: 2;
		width:100%;
		padding-top:85%;
		padding-bottom: 2%;
		text-align: center;
		white-space:nowrap;
		// height:0;
		position: relative;
		// background: #666;
		background:center top no-repeat;
		background-size: 85% auto;
		filter:opacity(50%);
	}
	>div.active {
		filter:none;
	}
	.bt_out {
		background-image: url('./img/icon/生成图片.png')
	}
	.bt_back {
		background-image: url('./img/icon/返回编辑.png')
	}
	.bt_save {
		background-image: url('./img/icon/长按保存.png');
		img {
			display: none;
			.fill();
			opacity: 0.01;
		}
		&.active img {
			display: block;
		}
	}
	.bt_share {
		background-image: url('./img/icon/马上分享.png')
	}
}

#bgbox.hide, #imgbox.hide{
	display:none;
}

#bgbox ul {
	display: flex;
	width: 100%;
	overflow: auto;
	padding: 4% 0;
	justify-content: space-between;
	> li {
		flex: 0 0 auto;
		display: inline-block;
		border-radius: 50%;
		// border: 2px solid #fff;
		width: 10%;
		padding-top: 10%;
		margin: 0 1%;
		height: 0;
	}
}
.deleteBtn {
	display: block;
	position: absolute;
	width: 22px;
	height: 22px;
	background: #f30;
	cursor: pointer;
	border-radius: 50%;
	overflow: hidden;
	z-index: 100;
	&::before,
	&::after {
		content: '';
		position: absolute;
		width: 70%;
		height: 2px;
		background: #fff;
		top: 50%;
		left: 50%;
	}
	&::before {
		transform: translate(-50%, -50%) rotate(45deg);
	}
	&::after {
		transform: translate(-50%, -50%) rotate(-45deg);
	}
}
