#selectImgBox {
	position: fixed;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-content: space-between;
	justify-content: space-evenly;
	align-content: space-evenly;
	left: 0;
	right: 0;
	bottom: 0;
	padding: 3% 0 8% 0;
	background: rgba(0, 0, 0, 0.7);
	transition: all 0.3s ease-out;
	// user-select: none;
	&.hide {
		opacity: 0;
		transform: translate(0,100%);
	}
	&::after{
		content: '按住选定元素长按拖动至绘板';
		position: absolute;
		font-size: 4vw;
		line-height: 2;
		text-align: center;
		color: #CCC;
		bottom:0;
		left:0;
		right:0;
	}
	.item {
		width: 20vw;
		height: 20vw;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		vertical-align: middle;
		position: relative;
		&::after{
			content: '';
			position: absolute;
			left:0; top:0; width:100%; height:100%;
		}
		img {
			flex: 0 0 auto;
			touch-action: none;
			max-width: 90%; 
			max-height: 90%;
			transition: all 0.5s cubic-bezier(.64,1.13,.85,1.35);
			// object-fit: contain;
			// display: block;
			&.loading{
				transform: scale(0);
				opacity: 0;
			}
		}
	}
}
.gu-mirror {
	&.item{
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		vertical-align: middle;
		img {
			flex: 0 0 auto;
			max-width: 90%; 
			max-height: 90%;
			// object-fit: contain;
			// display: block;
		}
	}
	&.lian{
		transform: scale(3.2)
	}
	&.yan{
		transform: scale(0.5)
	}
	&.fa{
		transform: scale(2)
	}
	&.kou{
		transform: scale(0.8)
	}
	&.bi{
		transform: scale(0.5)
	}
}
